"use strict";

var modal = require('../../packages/makeup-modal');

var modal1 = document.getElementById('modal-1');
var modal2 = document.getElementById('modal-2');
var modal3 = document.getElementById('modal-3');
var button1 = document.getElementById('button-1');
var button2 = document.getElementById('button-2');
var button3 = document.getElementById('button-3');
var unmodalButton = document.getElementById('button-unmodal');
var hoistCheckbox = document.getElementById('hoist-checkbox');
var wrapCheckbox = document.getElementById('wrap-checkbox');
var hiddenCheckbox = document.getElementById('hidden-checkbox');
modal1.addEventListener('makeup-modal', function (e) {
  return console.log(e);
});
modal2.addEventListener('makeup-modal', function (e) {
  return console.log(e);
});
modal3.addEventListener('makeup-modal', function (e) {
  return console.log(e);
});
modal1.addEventListener('makeup-unmodal', function (e) {
  return console.log(e);
});
modal2.addEventListener('makeup-unmodal', function (e) {
  return console.log(e);
});
modal3.addEventListener('makeup-unmodal', function (e) {
  return console.log(e);
});
button1.addEventListener('click', function () {
  modal.modal(modal1, {
    hoist: hoistCheckbox.checked,
    useHiddenProperty: hiddenCheckbox.checked,
    wrap: wrapCheckbox.checked
  });
});
button2.addEventListener('click', function () {
  modal.modal(modal2, {
    hoist: hoistCheckbox.checked,
    useHiddenProperty: hiddenCheckbox.checked,
    wrap: wrapCheckbox.checked
  });
});
button3.addEventListener('click', function () {
  modal.modal(modal3, {
    hoist: hoistCheckbox.checked,
    useHiddenProperty: hiddenCheckbox.checked,
    wrap: wrapCheckbox.checked
  });
});
unmodalButton.addEventListener('click', function () {
  modal.unmodal();
});